"use strict";

import Web3 from "web3";
import store from "../../store";
import { ACTION_TYPES, APPROVED_NETWORK_ID } from "../constants.js";

//监听web3
const monitorWeb3 = function(state) {
  let networkId = state && state.web3 ? state.web3.networkId : "";
  let coinbase = state && state.web3 ? state.web3.coinbase : "";//收取挖矿奖励的 coinbase 地址
  let web3 = window.web3;
  let isLocalWeb3 = false;
  
  // Checking if browser is Web3-injected (Mist/MetaMask)
  ////检查浏览器是否已注入Web3（Mist / MetaMask）
  console.dir("=========>");
  console.dir(web3);
  if (typeof web3 !== "undefined" && web3&&web3.isInjected) {
    // Use Mist/MetaMask's provider
    //使用Mist / MetaMask的提供程序
    web3 = new Web3(window.ethereum);
    window.ethereum.request({ method: 'eth_requestAccounts' })
    //window.ethereum.enable();
  } else {
    console.log("monitorWeb3: No web3 in browser");//monitorWeb3：浏览器中没有web3
    web3 = new Web3(new Web3.providers.HttpProvider(`https://ropsten.infura.io/v3/0c23855fdde64855986fd08b858f1906`));//使用本地节点
    isLocalWeb3 = true;
  }

  if (web3) {
    /* eslint-disable no-unused-vars */
    / *禁止禁用未使用的变量* /
    //新的块头
    web3.eth.subscribe("newBlockHeaders", function(error, result) {
      if (!error) {
        // console.log(result);
        // console.dir("newBlockHeaders");
        // console.dir(result);
      }
    });
  }

  setInterval(() => {
    //console.dir(store);
    if (web3 && !isLocalWeb3) {
      web3.eth.net.getId((err, newNetworkId) => {
        newNetworkId = !err && newNetworkId ? newNetworkId.toString() : "";
        if (
          (!err &&
            newNetworkId &&
            newNetworkId !== "" &&
            newNetworkId !== networkId) ||
          (!newNetworkId && networkId)
        ) {
          store.dispatch(ACTION_TYPES.LOGOUT);//退出登陆
          window.location.reload();//重装
        } else {
          //返回 地址
          web3.eth.getCoinbase((err, newCoinbase) => {
            newCoinbase = !err && newCoinbase ? newCoinbase.toString() : "";
            const approvedNetworkId = APPROVED_NETWORK_ID || newNetworkId;
            if (
              (!err &&
                newCoinbase &&
                newCoinbase !== "" &&
                newCoinbase !== coinbase && //切换了节点
                approvedNetworkId &&
                newNetworkId === approvedNetworkId) || //切换了节点
              (!newCoinbase && coinbase)
            ) {
              store.dispatch(ACTION_TYPES.LOGOUT);//退出登陆
              window.location.reload();
            } else if (
              !err &&
              newCoinbase &&
              newCoinbase !== "" &&
              newCoinbase !== coinbase
            ) {
              coinbase = newCoinbase;//收取 地址 
              //更新Web3属性
              store.dispatch(ACTION_TYPES.UPDATE_WEB3_PROPERTIES, {
                properties: ["coinbase"],
                values: [newCoinbase]
              });
            }
          });
        }
      });
    }
  }, 666);
};

//监控Web3
export default monitorWeb3;
