  </div>
</template>
<script>
export default {
  name: "App",
  components:{
    loading,
    tips
  },
  //计算属性
  computed: {
    //es6展开运算符 可以用在array和object上都行
    //共享资源状态管理
    ...mapState({
      //已注入Web3
      hasInjectedWeb3: state => state.web3.isInjected,
      //具有Web3注入浏览器
      hasWeb3InjectedBrowser: state => state.user.hasWeb3InjectedBrowser,
      //已连接到批准的网络
      isConnectedToApprovedNetwork: state =>
        state.user.isConnectedToApprovedNetwork,
      //是否有地址  
      hasCoinbase: state => state.user.hasCoinbase,
      networkId: state => state.web3.networkId,
      //地址
      coinbase: state => state.web3.coinbase,
      //当前路由
      currentRoute: state => state.currentRoute,
      //当前页面
      currentView: state => state.currentView,
      //用户
      user: state => state.user,
      //是否已准备好DApp
      isDAppReady: state => state.isDAppReady,
      //是有效用户
      isValidUserBut: state => state.isValidUserBut,
      //原来是有效用户
      originalIsValidUserBut: state => state.originalIsValidUserBut,
      //图形网址
      gravatarURL: state => state.gravatarURL,
      //头像画布
      avatarCanvas: state => state.avatarCanvas,
      //默认路由
      defaultRoute: state => state.defaultRoute
    })
  },
  data: function() {
    return {
      managers: { UserManager,NewsageManager },
      
    };
  },
  //computed 计算属性 擅长处理的场景：一个数据受多个数据影响
  //watch擅长处理的场景：一个数据影响多个数据
  watch: {
    
    //已注入Web3
    hasInjectedWeb3(web3ConnectionValue) {
      console.log("App:::hasInjectedWeb3: ", web3ConnectionValue);
    },
    networkId(networkId) {
      console.log("App:::networkId: ", networkId);
    },
    //地址
    coinbase(coinbase) {
      console.log("App:::coinbase: ", coinbase);
    },
    //是否已准备好DApp
    isDAppReady(isDAppReady) {
      console.log("App:::isDAppReady: ", isDAppReady);
      // this.callSetIsValidUserBut(
      //   this.$route.query.isValidUserBut || this.forcedIsValidUserBut
      // );
    },
    //路由
    $route(newRoute) {
      this[ACTION_TYPES.CHANGE_CURRENT_ROUTE_TO](newRoute);
      this[ACTION_TYPES.SET_CURRENT_VIEW](newRoute);
      // const isValidUserBut = this.$route.query.isValidUserBut;
      // if (isValidUserBut) {
      //   this.callSetIsValidUserBut(isValidUserBut);
      // } else {
      //   this.callResetIsValidUserBut();
      // }
    }
  },
  //vue 创建之前
  beforeCreate: function() {
    this.$store
    //dispatch 存储：this.$store.dispatch('initUserInfo',friend);  取值：this.$store.getters.userInfo;
    //web3
      .dispatch(ACTION_TYPES.REGISTER_WEB3_INSTANCE)//调度 注册WEB3实例
      
      //.dispatch("getLocalWeb3")
      .then(result => {
        let state = result.state;
        monitorWeb3(state);//监控Web3 变动
        // this.$store
        //   .dispatch(ACTION_TYPES.UPDATE_USER_BLOCKCHAIN_STATUS)//更新用户区块链状态
        //   .then(() => {
        //     if (!this.isDAppReady) {//是否已准备好DApp
        //       this.forcedIsValidUserBut = "0";//强制是有效用户
        //       this.$store.dispatch(ACTION_TYPES.UPDATE_DAPP_READINESS, true);//更新DApp准备情况
        //     }
        //   })
        //   .catch(() => {
        //     console.log("App:::Unable to UPDATE_USER_BLOCKCHAIN_STATUS");//应用程式:::无法执行更新用户区块链状态
        //     if (!this.isDAppReady) {
        //       this.$store.dispatch(ACTION_TYPES.UPDATE_DAPP_READINESS, true);//更新DApp准备情况
        //     }
        //   });
      })
      .catch((result = {}) => {
        let state = result.state;
        // this.forcedIsValidUserBut = "0";//强制是有效用户
        monitorWeb3(state);//监控Web3
        // if (!this.isDAppReady) {//是否已准备好DApp
        //   this.$store.dispatch(ACTION_TYPES.UPDATE_DAPP_READINESS, true);//更新DApp准备情况
        // }
        this.displayLoding({ isloading: false });
        this.$router.push(`/Metamask`);
        console.error(result, "App:::Unable to REGISTER_WEB3_INSTANCE");//应用程式:::无法执行注册WEB3实例
      });
  },
  //vue 创建时
  created: function() {
    this[ACTION_TYPES.CHANGE_CURRENT_ROUTE_TO](this.$route);//将state中当前路由更改为当前路由
    this[ACTION_TYPES.SET_CURRENT_VIEW](this.$route);//将state中页面设置当前视图
  },
  //事件处理器 挂载在对象上的函数，通常是Vue实例本身或Vue组件
  methods: {
    //es6展开运算符 可以用在array和object上都行
    //事件map
    ...mapActions([
      ACTION_TYPES.CHANGE_CURRENT_ROUTE_TO,//将当前路由更改为
      
      ACTION_TYPES.SET_CURRENT_VIEW,//设置当前视图
      // ACTION_TYPES.LOGIN,
      "displayLoding",
    ]),
    
    
   
    
    //调用访问区块链
    callToAccessBlockchain(payload = null) {
      //合约执行参数
      const actionParams = Object.assign({}, payload.requestParams, {//传过来的requestParams对象 参数
        methodName: payload.methodName,//执行合约的方法 owner
        contractIndexToUse: payload.contractIndexToUse,//执行合约名称 ,Migrations
      });
      const value = payload.value;//支付值
      this.managers[payload.managerIndex || "UserManager"]
      //访问区块链
        .accessBlockchain(this.$store.state, actionParams, value,payload.pairtType)
        .then(responseObject => {
          if (payload.callback) payload.callback(responseObject);
        })
        .catch(err => {
          if (payload.callback) payload.callback(false);
          console.error(
            err,
            //您可能不需要对此错误进行任何注意。 注册页面加载可能会引发此错误，但一切都很好。
            `App:::Unable to ${payload.methodName}. You may not need to pay any attention to this error. A page load on Sign Up may throw this error, but everything is fine.`
          );
        });
    },
   


  }
};

import { mapState, mapActions } from "vuex";
import { ACTION_TYPES } from "./util/constants";
import UserManager from "./js/UserManager";
import monitorWeb3 from "./util/web3/monitorWeb3";
import NewsageManager from "./js/NewsageManager";
    import loading from './components/loding';
    import tips from './components/tips'
</script>
