export const HTML_HOME_QA = {
  CN:{
    TITLE:`常见问题`,
    LIST:[
      {TITLE:`1.Newsage是什么？`,BODY:`
      Newsage是您永远取之不尽的财富制造机。
      它是建立在以太坊上，通过智能合约多轮次和永续运行，让大部分参与者获利，在铸造、交易和销毁中恒定上涨的数字通证系统。
      也就是说，Newsage兼顾了先来、后到等等大部分会员的利益。
      若非贪婪，即可盈利。
      下面的问答涵盖了机制和规则、操作和建议、开发和运行等方面，帮助您选择和操作。`},
      {TITLE:`2.Newsage与传统数字资产、直销矩阵有什么不同？`,BODY:`
      Newsage是全球首款财富创造和分享系统。
      与传统数字资产、直销矩阵不同，Newsage开发和运行的目的：永续创造财富并与所有参与者分享。因此，Newsage有着以下与传统模式迥异的机制：
      针对所有购币、挖矿的投资者的财富创造机制（通过NETO通证铸造生成财富）和通证恒涨机制（通过铸造后超额回购注销推升币价）。
      针对率先注册购买会员的数字矩阵分享机制（高层级分享低层级的购买、先到者分享后来者的购买）；针对后续注册购买会员的递增挖矿机制（后来者挖矿获得NETO通证奖励）；以及针对末位和靠近末位会员的末位大奖机制（一轮结束，最后的购买者赢得大奖且所有会员按倒序获挖矿奖励）。
      针对合作伙伴的团队排名抢位机制（最大的合作伙伴在下一轮赢得头部ID=1）。
      所有这些机制以智能合约形式在以太坊区块上永续运行，源源不断为会员创造财富。`},
      {TITLE:`3.Newtoken通证（NETO）是什么？为什么会恒定上涨？`,BODY:`
      Newsage拥有一套完整的财富创造和分享机制，这些机制的基础就是Newtoken通证，代码为NETO。
      NETO通证的铸造主要有两个途径，一是倒计时开始前的挖矿，即每次购买，平台都会根据购买金额和总进度百分比给予购买者NETO通证奖励；二是末位大奖，即每一轮结束后，平台会给予最后一个投注、以及按投注的倒序给予所有投注者以NETO通证奖励。
      关键在于，平台在NETO通证铸造发放后即实施铸造金额1.667倍的回购和注销，鉴于发行和回购注销的价格锚定物均为Uniswap上的NETO-ETH币对，因此整个过程均会造成NETO通缩、推动币价上涨、从而让持币人获益的。
      因此，Newtoken通证（NETO）交易价格在算法驱动下一定是长期恒定上涨的，它是针对所有购币、挖矿的会员和投资者的财富加速器。`},
      {TITLE:`4.怎样获得NETO通证？`,BODY:`
      持有Newsage的NETO通证是一种低风险和高回报的投资模式。
      获得NETO通证有两种途径：
      其一是点击“购币”直接在Uniswap上输入合约地址购买（为防止假币诈骗，不建议输入通证名称搜索购买）。
      其二就是参与Newsage，通过购买挖矿、或钻石挖矿、或末位大奖获得。请注意，在倒计时开始之前，选择任意X3或X4矩阵的任意层级购买，平台即按照购买金额最高13.2%比例将挖矿奖励NETO通证划转到购买的钱包地址中；但是，倒计时开始后，所有购买则不再有挖矿奖励了。`},
      {TITLE:`5.Newsage的分享规则是怎样的？`,BODY:`
      Newsage的会员分享采用精密的数字矩阵分享规则。它模拟了X3和X4两个矩阵、且每个矩阵都有12个层级；在X3矩阵中，每个层级有3个位置；在X4矩阵中，每个层级有4个位置。会员可选择任何一个矩阵、任何一个层级由低到高的逐级购买；但当某个矩阵已满时，平台将自动打开一个相同类型的新矩阵；每个层级的购买费用正好是前一个层级的2倍，并且您也可以从后续会员购买中获得2倍的收入。
所有会员根据注册的前后次序排序并给于唯一ID（推荐码），后续会员购买金额的67%将在层级较高的位置和排位靠前的会员中分享。
`},
      {TITLE:`6.怎样参与Newsage并且获利？`,BODY:`
      两种参与方式均可获得收益：一、注册购买Newsage，二、投资买卖NETO通证。
方式一注册购买，点击页面上方左侧的“注册”、进入自己的钱包进行授权、然后跳转登录界面进行购买。需要注意的是：①需要提前注册Matemask钱包并购入以太币（购买时需要支付Gas费用，一般约0.02ETH）。②每次购买同样要向以太坊公链支付智能合约运行的Gas费用（约0.05ETH），如果每次购买金额过低将不划算。
方式二投资购买通证，点击页面上方中间的购买、进入自己的钱包，然后点击Uniswap、输入（粘贴）NETO-ETH币对的合约地址******，即可买卖NETO通证。
      `},
      {TITLE:`7.怎样判断和选择最好的时机以获利？`,BODY:`
      即刻参与，任何阶段在Newsage都有获利良机。
会员通过查看实时数据和提示，结合自己的判断做出购买或投资组合。具体的，一个轮次可以分为倒计时前、倒计时后两个阶段，前阶段又分为创世、初始、上升和成熟四步。会员登录后可以看到本轮进展情况，并在不同阶段侧重不同的购买技巧。
在倒计时之前，平台给出的参考数据，其中较为重要的：
①进度：现总购买额除以倒计时截止额的百分比；向您标识出与倒计时的距离、以及自己在本轮中的大致位置。
②挖矿奖励比：现阶段会员挖矿收入（NETO通证金额）除以本次购买金额的百分比，最高可到13.2%；向您标识出购买后即可获得的NETO通证金额。
③团队排名：通过输入邀请码注册的会员与该邀请码会员互为合作伙伴关系，所有具有合作伙伴关系的会员为一个团队。平台将每个独立团队的所有会员的购买额相加，由大到小排序；向您标识出次轮您所在团队作为头部位置（ID=1）的可能性（排名第一的团队的第一位会员将占据次轮的头部位置）。
在倒计时之后，平台增加的参考数据，其中较为重要的：
①倒计时：进入倒计时后，最后一个购买后距离结束的时间；向您标识出可能赢取末位大奖的可能性。
②末位大奖总金额（预测）：截止当前时刻，奖金池中归属末位大奖的总金额。
③钻石雨预警：一轮结束约12小时后，平台启动回购和注销操作，此时对所有登录用户给出该提醒。
      `},
      {TITLE:`8.如果我从没交易过虚拟币，那么一切从哪里开始呢？`,BODY:`
      人类社会正在进入数字资产时代，交易虚拟币是必由之路。
您首先需要注册钱包并购入加密货币（如ETH），而如imToken、Tokenpocket等钱包都有以法定货币交换虚拟货币的OTC业务，它们都是为普通用户设计的，具有友好的交互界面和便捷操作。建议您动手之前，先从网上搜索观看相关钱包注册、私钥保存和换币交易的教学视频，透彻了解和掌握流程和注意事项之后再进行操作。
重点提醒：①钱包的私钥是加密货币唯一的持有凭证，注册钱包后要立即妥善保管好（建议不要用手机拍照、截屏等方式保存私钥，最好采用手写抄录多份纸质保存）。②OTC（场外交易）换币业务在某些国家是受限具有一定风险的。

      `},
      {TITLE:`9.注册以后，如何进行购买和复投？`,BODY:`
      倒计时前和倒计时后的获利模式不同，因此可以考虑不同的购买和复投。
倒计时前，购买同时兼顾NETO通证买入：创世阶段可两个矩阵均衡投入，赢取后续会员分享；初始阶段购入部分NETO通证等待超额回购注销上涨；上升阶段如下线会员多则多买X3矩阵，反之多买X4矩阵；成熟阶段挖矿收益率将超过13%，兼顾购买挖矿、通证购买。
倒计时后，NETO通证买入同时兼顾购买：进入倒计时，本轮购买就要临近尾声了，考虑到一轮结束后超额回购注销造成的通证价格拉升，可以适当买入；同时，末位大奖是一笔非常诱人的奖励，其金额约在总购买额的5-10%，在倒计时开始后，关注倒计时时长，适时购买有可能赢得末位大奖。
      `},
      {TITLE:`10.如何查看盈利金额？我需要从Newsage提款吗？`,BODY:`
      注册后登录进入的操作界面上，包括本轮的购买、收入（ETH和NETO）和盈利数据都实时展示。
您不需要任何提款操作，所有的购买分享、挖矿收入、末位大奖、挖矿钻石等，Newsage都实时划转到您的钱包地址。您可以点击查看，且只有您可以使用自己的钱包。
      `},
      {TITLE:`11.如何在下一轮中抢占头部位置（ID=1）？`,BODY:`
      平台将次轮的头部位置奖励给排名第一的团队的首位会员。
具体的，注册时在“我与推荐ID是否团队关系？”选择“是”的会员之间构成团队关系，所有团队成员的购买额合计排名，本轮结束后，排名第一的团队首位注册人（钱包地址）将自动作为下一轮的头部（ID=1）。
特别对于有合作伙伴关系的团队而言，最初加入的合伙人务必在选项中勾选“否”，而团队的其他人，均必须手动填写自己合伙人的推荐码、勾选“是”。你在团队所处的位置和团队合伙人数，以及所在团队总购买额的排名，将实时显示出来。
这里划重点：务必让你的合伙人在注册时填写你的推荐码（ID）并勾选“是”。
      `},
      {TITLE:`12.怎样挖矿？各类收益怎么计算呢？`,BODY:`
      平台针对倒计时前的投注，按照递增比例给予挖矿奖励，计算公式为：挖矿NETO通证奖励金额=本次购买额*13.2%*总进度百分比，因此只要在矩阵中购买任何席位都将获得挖矿奖励。
Newsage平台共有四种收益，分别为：矩阵分享收益、挖矿收益、末位大奖收益和钻石挖矿收益，每种收益都有单独的计算方法和公式。
为了给您以简单和直观的参考，平台提供了“模拟一下”的功能，您只需要输入拟购买金额（不能超过28ETH）、点击“模拟一下”，平台就将各项收益数据更新在“我的数据仪表盘”上了。
      `},
      {TITLE:`13.倒计时开始后，如何操作才能赢得末位大奖？`,BODY:`
      平台重奖最末位的购买会员。进入倒计时后，每一个购买之后即开始倒计时，如果倒计时的减少为0还没有新的购买，则该购买会员赢得末位大奖，并与其他会员按投注的倒序共同分享奖金池剩余的所有奖金。
具体计算方法：
所有投注均按如下方式赋予权重：第1个投注权重为3*0.75、第2个为4*0.75、第3个为5*0.75...依次类推，则倒数第2个投注为N*0.75，最后一个投注的权重定义为（3+N）*（N-3+1）/2*0.25
*注：最后一注权重大致为全部投注权重之和的25%
再分五步计算出各个投注所得到的NETO通证金额：
第一步，依次将投注金额乘以投注权重。即第1个投注金额乘以投注权重、再将第2个第3个...一直到最后一个投注金额乘以投注权重。
第二步，再将以上所有乘积求和。
第三步，计算出末位大奖的总NETO通证数，即末位大奖总金额*60%
第四步，用末位大奖的总NETO通证数，除以第二步得出的和。
第五步，第四步所得的商乘以每个投注的投注金额、再乘以其投注权重，得到的乘积即为该投注的末位大奖的NETO通证金额。
按以上计算结果铸造、发放NETO通证。
另外，每次购买后的倒计时时长的计算方式为：第（N+1）次倒计时时长=第N次倒计时时长-1S，第1次倒计时时长按照1800S计，倒计时时长最小为180S。
      `},
      {TITLE:`14.怎么知道下一轮什么时候开始？`,BODY:`
      在次轮中抢占靠近头部（ID=1）的位置，将获得较多后续位置会员的购买分享。
了解次轮开启的时间很简单，就是紧盯倒计时的时长，当倒计时时长缩短到180秒也就是3分钟之后，基本上离本轮结束就不会太久了（甚至此时你也可以复投试试运气），当平台提示本轮购买结束时，就可以马上进行购买了。
当然，即使倒计时时长仅为120秒也可能持续很长时间才能决出末轮大奖、从而重启次轮购买。
      `},
      {TITLE:`15.怎样进行钻石挖矿？`,BODY:`
      钻石挖矿是简单和高回报的挖矿，但是需要一点点运气。
平台开启钻石挖矿是为了在一轮购买结束后的12小时启动回购、注销NETO通证的操作，并且每隔3分钟重复一次、每次回购注销价值4ETH的通证，直到奖金池中所有资金耗尽。
在平台提示“前方有钻石雨”之后，您可点击“挖钻石”、进入钱包界面，然后进行平台显示Gas费用的支付，完成后平台即将金额为0.17ETH（约为Gas费的15倍）的NETO通证划转到您钱包中。
      `},
      {TITLE:`16.怎么购买或出售NETO通证？`,BODY:`
      您可随时便捷的购买或出售NETO通证，通证的流动性是其价值的体现。
Newsage在启动前即于Uniswap上设立了NETO-ETH币对，合约地址为****，点击进入Uniswap、输入（粘贴）该地址后，任何人都可以在该币对上买入或卖出NETO通证。
特别提醒：为防止假币欺诈，不建议您在Uniswap上输入NETO进行搜索后交易。
      `},
      {TITLE:`17.Newsage是谁开发和运营的？`,BODY:`
      Newsage属于一组区块链及加密技术爱好者，他们同时也是社区的成员，且没有任何特权；同样的，包括NETO通证，均无针对任何人的发行、预挖、赠送或奖励，完全由会员挖矿和奖励。
现在到永远，Newsage是独立运行在以太坊上的智能合约，是隶属于所有会员的点对点社区，没有任何个人或企业能够控制或影响其运行。
      `},
      {TITLE:`18.如何保证Newsage公开透明、不受操控、独立永续运行？`,BODY:`
      Newsage由以太坊上的智能合约，以及Gethub上的前端代码组成，由注册会员调用而自动执行，任何人不允许也不能够干预其中任何运行的过程和结果。
它是去中心化的，所有代码、数据都可以在以太坊区块浏览器上（地址链接）查询、验证，以确保Newsage的公开透明、不受控制、独立永续运行。
      `},
      {TITLE:`19.什么是以太坊？为什么把Newsage构架在以太坊上？`,BODY:`
      以太坊（ETH）是最为可靠和领先的公链之一，自2015年上线以来，即成为众多区块链应用的基础框架。构架在以太坊上的应用项目，不仅可以让您便捷浏览所有交易历史记录，还可以永久保存和运行其智能合约。
      `},
      {TITLE:`20.什么是智能合约？Newsage就是智能合约吗？`,BODY:`
      智能合约是运行在区块链虚拟机上的计算机程序，它是以算法为核心的、不可篡改的代码。在Newsage中，以太坊是搭载我们创建的智能合约的区块链公链，而智能合约、客户端和交易币对则构成了Newsage整体。
Newsage的智能合约完全、严格的按照透明机制构架会员之间的关系、自动执行彼此的财富创造和分配条约。
      `},
    
    ],
  },
  EN:{
    TITLE:`common problem`,
    LIST:[
      {TITLE:`1. What is Newsage?`,BODY:`
      Newsage is your inexhaustible wealth generator.
       It is a digital token system that is built on Ethereum, through multiple rounds and perpetual operation of smart contracts, allowing most participants to profit, and constantly rising in casting, trading and destruction.
       In other words, Newsage takes into account the interests of most members, such as first come, last come, etc.
       If you are not greedy, you can make a profit.
       The following questions and answers cover the mechanisms and rules, operations and suggestions, development and operation, etc., to help you choose and operate.
      `},
      {TITLE:`2. What is the difference between Newsage and traditional digital assets and direct selling matrix?`,BODY:`
      Newsage is the world's first wealth creation and sharing system.
      Different from traditional digital assets and direct selling matrix, the purpose of Newsage's development and operation is to create wealth forever and share it with all participants. Therefore, Newsage has the following mechanisms that are very different from the traditional model:
      Wealth creation mechanism (to generate wealth through NETO token casting) and constant increase mechanism (to increase the price of tokens through excess repurchase and cancellation after casting) for all investors who purchase coins and mine.
      Digital matrix sharing mechanism for members who are the first to register to purchase (high-level sharing of low-level purchases, first-comers to share purchases of latecomers); incremental mining mechanism for members who register for subsequent purchases (late-comers will receive NETO token rewards for mining) ; And the last prize mechanism for the last and near-last members (at the end of the round, the last buyer wins the grand prize and all members receive mining rewards in reverse order).
      Team ranking mechanism for partners (the largest partner will win the head ID=1 in the next round).
      All these mechanisms operate continuously on the Ethereum block in the form of smart contracts, continuously creating wealth for members.
      `},
      {TITLE:`3. What is Newtoken (NETO)? Why does it keep rising?`,BODY:`
      Newsage has a complete set of wealth creation and sharing mechanisms. The basis of these mechanisms is the Newtoken certificate, coded as NETO.
       There are two main ways for the casting of NETO tokens. One is mining before the countdown starts, that is, for each purchase, the platform will reward the purchaser with NETO tokens based on the purchase amount and the total progress percentage; the second is the last prize, which is every time After the end of the round, the platform will give the last bet and reward all bettors with NETO tokens in the reverse order of the bet.
       The key is that the platform will implement the repurchase and cancellation of 1.667 times the minted amount after the issuance of the NETO token. Since the price anchors for the issuance and repurchase cancellation are all NETO-ETH currency pairs on Uniswap, the entire process will be Causes NETO deflation, promotes the rise of currency prices, and thus benefits the holders of the currency.
       Therefore, the trading price of Newtoken (NETO) must be constantly rising for a long time under the algorithmic drive. It is a wealth accelerator for all members and investors who buy coins and mine.
      `},
      {TITLE:`4. How to get NETO token?`,BODY:`
      Holding Newsage's NETO token is a low-risk and high-return investment model.
       There are two ways to obtain NETO token:
       One is to click "Buy Coins" to directly enter the contract address on Uniswap to purchase (to prevent counterfeit currency fraud, it is not recommended to enter the token name to search for purchase).
       The second is to participate in Newsage, through purchase mining, or diamond mining, or the last prize. Please note that before the countdown starts, if you choose any level of X3 or X4 matrix to purchase, the platform will transfer the mining reward NETO tokens to the purchased wallet address according to the maximum 13.2% of the purchase amount; however, after the countdown starts, All purchases will no longer have mining rewards.
       `},
      {TITLE:`5. What are the sharing rules of Newsage?`,BODY:`
      Newsage's member sharing adopts sophisticated digital matrix sharing rules. It simulates two matrices X3 and X4, and each matrix has 12 levels; in the X3 matrix, each level has 3 positions; in the X4 matrix, each level has 4 positions. Members can choose any matrix and any level to purchase from low to high; but when a matrix is full, the platform will automatically open a new matrix of the same type; the purchase cost of each level is exactly the previous level And you can get twice the income from subsequent member purchases.
All members are sorted according to the order of registration and given a unique ID (recommendation code), and 67% of the purchase amount of subsequent members will be shared among the members with higher ranks and higher rankings.
`},
      {TITLE:`6. How to participate in Newsage and profit?`,BODY:`
      There are two ways to participate in the benefits: 1. Register to buy Newsage; 2. Invest in NETO tokens.
Method 1: Register to purchase, click "register" on the top left of the page, enter your wallet for authorization, and then jump to the login interface to purchase. It should be noted that: ① It is necessary to register the Matemask wallet in advance to purchase Ether (Gas fee is required when purchasing, generally about 0.02ETH). ②Each purchase also has to pay the gas fee (about 0.05ETH) for the operation of the smart contract to the Ethereum public chain. If the purchase amount is too low, it will not be cost-effective.
The second method is to invest and purchase tokens, click Buy in the middle of the top of the page, enter your wallet, then click Uniswap, enter (paste) the contract address of the NETO-ETH currency pair******, you can buy and sell NETO tokens.
      `},
      {TITLE:`7. How to judge and choose the best time to profit?`,BODY:`
      Participate immediately, and there are opportunities for profit in Newsage at any stage.
Members make purchases or investment portfolios by viewing real-time data and tips, combined with their own judgments. Specifically, a round can be divided into two stages before the countdown and after the countdown. The previous stage is further divided into four steps: creation, initial, ascent and maturity. After logging in, members can see the progress of this round and focus on different buying skills at different stages.
Before the countdown, the reference data given by the platform, of which the more important ones are:
①Progress: The percentage of the total purchase amount divided by the countdown deadline; Identifies the distance from the countdown and your approximate position in the current round.
②Mining reward ratio: the percentage of current member’s mining income (NETO token amount) divided by this purchase amount can be up to 13.2%; it will show you the amount of NETO token you can get after purchase.
③Team ranking: The member registered by entering the invitation code and the member of the invitation code are mutually cooperative, and all members with the cooperative partnership are a team. The platform adds up the purchases of all members of each independent team, sorting from largest to smallest; it identifies the possibility of your team as the head position (ID=1) in the next round (the first team’s position) One member will occupy the head position in the second round).
After the countdown, the platform added reference data, of which the more important ones are:
①Countdown: After entering the countdown, the time from the end of the last purchase; to identify the possibility of winning the last prize.
②Total amount of the last prize (forecast): As of the current moment, the total amount of the last prize in the prize pool.
③Diamond Rain Warning: Approximately 12 hours after the end of the round, the platform initiates repurchase and logout operations. At this time, this reminder will be given to all logged-in users.
      `},
      {TITLE:`8. If I have never traded virtual currency, where do I start?`,BODY:`
      Human society is entering the era of digital assets, and trading virtual currency is the only way.
You first need to register a wallet to acquire cryptocurrency (such as ETH). Wallets such as imToken and Tokenpocket have OTC services that exchange virtual currencies with legal currencies. They are all designed for ordinary users and have a friendly interactive interface and convenient operation. . It is recommended that you first search and watch the instructional videos on wallet registration, private key storage and currency exchange transactions from the Internet before you do it. You can thoroughly understand and master the process and precautions before proceeding.
Important reminder: ①The private key of the wallet is the only certificate of cryptocurrency. After registering the wallet, you should keep it properly (it is recommended not to save the private key with mobile phone to take pictures, screenshots, etc., it is best to copy multiple paper copies by hand) . ②OTC (Over-the-Counter Trading) currency exchange business is restricted in some countries and has certain risks.
      `},
      {TITLE:`9. How to purchase and reinvest after registration?`,BODY:`
      The profit model before and after the countdown is different, so different purchases and reinvestments can be considered.
      Before the countdown, purchase while taking into account the purchase of NETO tokens: two matrices can be balanced in the creation stage to win follow-up members to share; some NETO tokens purchased in the initial stage wait for excess repurchase to cancel and increase; in the rising stage Buy more X3 matrices, and vice versa; buy more X4 matrices; the mining yield in the mature stage will exceed 13%, taking into account the purchase of mining and token purchase.
      After the countdown, the purchase of NETO tokens will also take into account the purchase: entering the countdown, this round of purchase is coming to an end, considering the increase in the price of the token caused by the cancellation of excess repurchase after the end of the round, it can be bought appropriately; at the same time, at the end The grand prize is a very attractive reward, and its amount is about 5-10% of the total purchase amount. After the countdown starts, pay attention to the length of the countdown, and purchase in time may win the last prize. 
      `},
      {TITLE:`10. How to check the profit amount? Do I need to withdraw money from Newsage?`,BODY:`
      After registration, log in to the operation interface, including the current round of purchases, income (ETH and NETO) and profit data are displayed in real time.
You don't need any withdrawal operations, all purchase sharing, mining income, last prize, mining diamonds, etc., Newsage will be transferred to your wallet address in real time. You can click to view, and only you can use your wallet.
      `},
      {TITLE:`11. How to grab the head position (ID=1) in the next round?`,BODY:`
      The platform rewards the first member of the first-ranked team with the head position in the second round.
Specifically, during registration, members who selected "Is a team relationship with the referral ID?" formed a team relationship, and the total purchase amount of all team members was ranked. After this round, the first registrant of the team ranked first (Wallet address) will automatically be the head of the next round (ID=1).
Especially for a team with a partnership relationship, the initial partner must check "No" in the options, and the rest of the team must manually fill in their partner's recommendation code and check "Yes". Your position in the team and the number of team partners, as well as the ranking of the team's total purchases, will be displayed in real time.
Here is the key point: be sure to let your partner fill in your referral code (ID) and check "Yes" when registering.
      `},
      {TITLE:`12. How to mine? How to calculate various types of income?`,BODY:`
      For the betting before the countdown, the platform will give mining rewards in an increasing proportion. The calculation formula is: Mining NETO token reward amount = this purchase amount * 13.2% * total progress percentage, so as long as you buy any seat in the matrix, you will get Mining rewards.
The Newsage platform has four kinds of income, namely: matrix sharing income, mining income, final prize income and diamond mining income. Each income has its own calculation method and formula.
In order to give you a simple and intuitive reference, the platform provides a "simulate" function. You only need to enter the amount you plan to purchase (not more than 28ETH), click "simulate", and the platform will update the various income data in the "I Data Dashboard" is up.
      `},
      {TITLE:`13. After the countdown starts, how can I win the final prize?`,BODY:`
      The platform rewards the last purchase member. After entering the countdown, the countdown starts after each purchase. If the countdown is reduced to 0 and no new purchases are made, the purchasing member wins the last prize and shares all the remaining prize pool with other members in the reverse order of betting.
Specific calculation method:
All bets are weighted as follows: the first bet has a weight of 3*0.75, the second bet is 4*0.75, the third is 5*0.75... and so on, the penultimate bet is N*0.75 , The weight of the last bet is defined as (3+N)*(N-3+1)/2*0.25
*Note: The weight of the last bet is roughly 25% of the sum of all betting weights
Calculate the amount of NETO tokens obtained for each bet in five steps:
The first step is to sequentially multiply the bet amount by the bet weight. That is, the first bet amount is multiplied by the betting weight, and the second and third... until the last bet is multiplied by the betting weight.
The second step is to sum all the above products.
The third step is to calculate the total number of NETO tokens for the last big prize, that is, the total amount of the last big prize * 60%
The fourth step is to divide the total number of NETO tokens of the last big prize by the sum obtained in the second step.
In the fifth step, the quotient obtained in the fourth step is multiplied by the bet amount of each bet, and then multiplied by its betting weight, and the product obtained is the NETO token amount of the last prize of the bet.
NETO tokens are cast and issued based on the above calculation results.
In addition, the calculation method of the countdown duration after each purchase is: the (N+1)th countdown duration = the Nth countdown duration-1S, the first countdown duration is calculated as 1800S, and the minimum countdown duration is 180S.
      `},
      {TITLE:`14. How do I know when the next round will start?`,BODY:`
      In the second round, if you seize the position close to the head (ID=1), you will get more purchase shares from subsequent position members.
It’s very simple to understand the opening time of the second round. It is to keep an eye on the countdown time. When the countdown time is shortened to 180 seconds, which is 3 minutes, it will not be too long before the end of the round (even at this time you can also re-invest) Try your luck), when the platform prompts that the round of purchase is over, you can purchase immediately.
Of course, even if the countdown is only 120 seconds, it may take a long time to determine the final round of prizes and restart the next round of purchases.
      `},
      {TITLE:`15. How to mine diamonds?`,BODY:`
      Diamond mining is simple and highly rewarding mining, but requires a little bit of luck.
The platform starts diamond mining to start the repurchase and cancellation of NETO tokens 12 hours after the end of a round of purchase, and repeat it every 3 minutes, every time the tokens worth 4ETH are repurchased and cancelled, until all the tokens are in the bonus pool. The funds are exhausted.
After the platform prompts "Diamond rain ahead", you can click "Dig diamonds", enter the wallet interface, and then proceed to the platform display Gas fee payment. After completion, the platform will pay 0.17 ETH (approximately 15 times the Gas fee) NETO token is transferred to your wallet.
      `},
      {TITLE:`16. How to buy or sell NETO tokens?`,BODY:`
      You can easily buy or sell NETO tokens at any time, and the liquidity of the tokens reflects its value.
Newsage established the NETO-ETH currency pair on Uniswap before launching. The contract address is ****. After clicking to enter Uniswap and entering (pasting) the address, anyone can buy or sell on the currency pair. NETO token.
Special reminder: In order to prevent counterfeit currency fraud, it is not recommended that you enter NETO on Uniswap to search and trade.
      `},
      {TITLE:`17. Who develops and operates Newsage?`,BODY:`
      Newsage belongs to a group of blockchain and encryption technology enthusiasts. They are also members of the community and do not have any privileges. Similarly, including the NETO pass, there is no issuance, pre-mining, gifting or reward for anyone. Member mining and rewards.
Now and forever, Newsage is a smart contract running independently on Ethereum, a peer-to-peer community belonging to all members, and no individual or company can control or influence its operation.
      `},
      {TITLE:`18. How to ensure that Newsage is open, transparent, free from manipulation, independent and sustainable operation?`,BODY:`
      Newsage consists of the smart contract on Ethereum and the front-end code on Gethub, which is called by registered members and executed automatically. No one is allowed or able to interfere with any running process and results.
It is decentralized, and all codes and data can be queried and verified on the Ethereum block explorer (address link) to ensure that Newsage is open, transparent, uncontrolled, independent and sustainable.
      `},
      {TITLE:`19. What is Ethereum? Why is Newsage built on Ethereum?`,BODY:`
      Ethereum (ETH) is one of the most reliable and leading public chains. Since its launch in 2015, it has become the basic framework for many blockchain applications. Application projects built on Ethereum not only allow you to conveniently browse all transaction history records, but also permanently store and run its smart contracts.
      `},
      {TITLE:`20. What is a smart contract? Is Newsage a smart contract?`,BODY:`
      A smart contract is a computer program running on a blockchain virtual machine. It is an algorithm-centric code that cannot be tampered with. In Newsage, Ethereum is a blockchain public chain that carries the smart contracts we created, and smart contracts, clients, and transaction currency pairs constitute Newsage as a whole.
Newsage's smart contracts completely and strictly follow a transparent mechanism to structure the relationship between members and automatically execute each other's wealth creation and distribution treaties.
      `},
    
    ],
  },
};

